@font-face {
  font-family: "Cooper Hewitt";
  font-weight: normal;
  src: url("./font/CooperHewitt-Book.woff");
}
@font-face {
  font-family: "Cooper Hewitt Medium";
  src: url("./font/CooperHewitt-Medium.woff");
}

/* layout */

html {
  font-family: "Cooper Hewitt", serif;
  font-size: 1.2rem;
  line-height: 1.4em;
}

main {
  max-width: 28rem;
  margin: auto;
}

nav {
  margin-top: 2rem;
}

footer {
  margin-top: 2rem;
}

/* styles */

h1, h3, h5 {
  font-family: "Cooper Hewitt Medium";
}

h1, h2 { font-size: 2rem; }
h3, h4 { font-size: 1.5rem; }
h5, h6 { font-size: 1.2rem; }

p {
  margin-bottom: 1rem;
}

strong {
  font-family: "Cooper Hewitt Medium";
}

footer {
  font-size: 0.9rem;
}

/* custom */